import { Container, Heading, Text } from "@chakra-ui/react";
import PatternTextOverview from "./PatternTextOverview";
import { PlainTextSection } from "../utils/types";
import PlainTextRows from "./PlainTextRows";

export default function SectionInstruction({
  title,
  rowType,
  startRowID,
  rows,
  intro,
  beforeList,
  afterList,
  notes,
  showSide,
}: PlainTextSection) {
  return (
    <Container maxW="4xl" mt={6} mb={8} ml={0} mr={0} p={0}>
      {title && (
        <Heading as="h3" size="md" mt={2} mb={3}>
          {title}
        </Heading>
      )}
      {intro && <Text mb={3}>{intro}</Text>}
      {beforeList != null && (
        <Container mb={4} ml={0} mr={0} p={0}>
          <PatternTextOverview overview={beforeList} />
        </Container>
      )}
      {rows.length > 0 && (
        <PlainTextRows
          rowType={rowType}
          rows={rows}
          startRowID={startRowID}
          showSide={showSide}
        />
      )}
      {afterList != null && (
        <Container mb={4} ml={0} mr={0} p={0}>
          <PatternTextOverview overview={afterList} />
        </Container>
      )}
      {notes && <Text>{notes}</Text>}
    </Container>
  );
}
