import { Text, Flex, VStack, HStack } from "@chakra-ui/react";
import { cx, css } from "@linaria/core";
import { Stitch } from "../utils/types";
import GlyphWrapper from "./glyphs/GlyphWrapper";
import definition from "./definition";
interface Props {
  stitches: Stitch[];
  hasWS?: boolean;
}

const styles = {
  wrapper: css`
    border: 1px solid var(--chart-color);
    max-width: min-content;
  `,
};

function Description({ side, sts }: { side: string; sts: Stitch }) {
  return (
    <HStack align="top">
      <Text variant="bold">{side}</Text>
      <Text variant="code">{sts.label}</Text>
      <Text>
        {sts.instruction}
        {side === "RS: " ? "," : ""}
      </Text>
    </HStack>
  );
}

export default function StitchList({ stitches, hasWS = false }: Props) {
  return (
    <VStack
      align="left"
      gap={2}
      style={{ border: "1px solid var(--oc-gray-4)" }}
      padding={4}
      borderRadius={8}
      width="fit-content"
    >
      {stitches.map((sts, i) => (
        <Flex key={i} align="center" wrap="wrap" gap={2}>
          <GlyphWrapper sts={sts} className={cx(styles.wrapper)} />
          <Description side={hasWS && sts.ws != null ? "RS: " : ""} sts={sts} />
          {hasWS && sts.ws != null && (
            <Description side="WS: " sts={definition[sts.ws]} />
          )}
        </Flex>
      ))}
    </VStack>
  );
}
