import React, { lazy, Suspense } from "react";
import { useParams } from "react-router-dom";
import { usePatternBySize } from "../layout/PatternBySize";

export default function PatternPart() {
  const { menu, size } = usePatternBySize();

  const param = useParams();

  const Instruction = lazy(
    () => import(`../projects/${param.projectID}/Instruction`)
  );

  return (
    <Suspense fallback={<div>Loading instruction...</div>}>
      <Instruction menu={menu} size={size} />
    </Suspense>
  );
}
