import { Text } from "@chakra-ui/react";
import type { Chart, ChartRow, Direction } from "../utils/types";
import { css, cx } from "@linaria/core";
import { unit } from "./styles";
import { patternStyles, motifColors } from "./styles";
import { FocusType } from "./Chart";
import { isWSRow } from "../utils";
import { getHumanReadableSts } from "../utils/stsRenderer";

type Display = "inline" | "block";
interface Props {
  chart: Chart;
  direction?: Direction;
  focus?: FocusType;
  startRowID?: number;
  display?: Display;
}

const styles = {
  // Text instruction
  block: css`
    display: flex;
    flex-wrap: wrap;
  `,
  row: css`
    display: flex;
    align-items: baseline;
    margin-bottom: ${unit}px;
  `,
  rowBlock: css`
    margin-bottom: ${unit * 2}px;
    margin-right: ${unit * 2}px;
    border: 1px solid var(--oc-gray-4);
    padding: ${unit * 2}px;
    border-radius: ${unit}px;
  `,
  rowRS: css`
    display: flex;
    div {
      &:not(:last-child) {
        &:after {
          content: ", ";
        }
      }
    }
  `,
  rowRSBlock: css`
    display: flex;
    flex-direction: column;
    margin-top: ${unit}px;
    div {
      width: fit-content;
      white-space: nowrap;
    }
  `,
  rowWS: css`
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    div {
      &:not(:first-child) {
        &:after {
          content: ", ";
        }
      }
    }
  `,
  rowWSBlock: css`
    display: flex;
    flex-direction: column-reverse;
    margin-top: ${unit}px;
    div {
      width: fit-content;
      white-space: nowrap;
    }
  `,
  textSts: css`
    margin-right: ${unit / 4}px;
    padding-left: ${unit / 4}px;
    padding-right: ${unit / 4}px;
  `,
};

export function WrittenRow({
  row,
  rowID,
  direction,
  display,
  focus,
  className,
}: {
  row: ChartRow;
  rowID: number;
  direction: Direction;
  display: Display;
  focus?: FocusType | undefined | null;
  className?: string;
}) {
  return (
    <div
      className={cx(
        isWSRow(direction, rowID)
          ? display === "inline"
            ? styles.rowWS
            : styles.rowWSBlock
          : display === "inline"
          ? styles.rowRS
          : styles.rowRSBlock,
        className
      )}
    >
      {row.map((stsGroup, j) => {
        const stsGroupText = getHumanReadableSts(
          stsGroup.sts,
          isWSRow(direction, rowID)
        );
        const { motif } = stsGroup;
        return (
          stsGroupText.length > 0 && (
            <div
              key={j}
              className={cx(
                styles.textSts,
                rowID === focus?.[0] &&
                  j === focus?.[1] &&
                  patternStyles.selectedSts,
                motif?.id != null && motifColors[motif?.id]
              )}
            >
              {stsGroupText.join(", ")}
            </div>
          )
        );
      })}
    </div>
  );
}

export default function Written({
  chart,
  direction = "round",
  focus,
  startRowID = 0,
  display = "inline",
}: Props) {
  return (
    <div className={cx(display === "block" && styles.block)}>
      {chart.map((row, i) => (
        <div
          key={i}
          className={cx(display === "inline" ? styles.row : styles.rowBlock)}
        >
          <Text variant="code" mr={2}>
            {direction === "round" ? "Rnd" : "Row"} {i + startRowID + 1}
          </Text>
          <WrittenRow
            row={row}
            rowID={i}
            direction={direction}
            display={display}
            focus={focus}
          />
        </div>
      ))}
    </div>
  );
}
