import { Card, CardBody, Heading, Text, Image, Stack } from "@chakra-ui/react";

interface Props {
  title: string;
  list: string[];
  photo?: string;
}
export default function ProjectCard({ title, list, photo }: Props) {
  return (
    <Card
      maxW={240}
      variant="elevated"
      style={{ borderRadius: 0, backgroundColor: "white" }}
    >
      {photo != null && (
        <Image
          objectFit="cover"
          src={`${process.env.PUBLIC_URL}/photos/${photo}`}
          alt={title}
        />
      )}
      <CardBody>
        <Heading as="h2" size="md">
          {title}
        </Heading>
        <Stack spacing={1} pt={3}>
          {list.map((item, i) => (
            <Text key={i}>{item}</Text>
          ))}
        </Stack>
      </CardBody>
    </Card>
  );
}
