import { Box, Divider } from "@chakra-ui/react";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { PageInfo } from "../utils/types";
import PageMenu from "../components/PageMenu";

interface Props {
  page: string;
}

export default function Stitches({ page }: Props) {
  const [pageInfo, setPageInfo] = useState<PageInfo[] | null>(null);
  const [selectedMenu, setSelectedMenu] = useState<number>(0);
  const param = useParams();

  // Load project info
  useEffect(() => {
    (async () => {
      const info = await import(`../projects/${param.projectID}/${page}`);
      setPageInfo(info.default);
      const menuID = info.default
        ?.map((s: { id: string }) => s.id)
        .indexOf(param["*"] ?? "");
      setSelectedMenu(Math.max(menuID ?? 0, 0));
    })();
  }, [param, page]);

  if (pageInfo == null) {
    return null;
  }

  return (
    <Box
      style={{ background: "white" }}
      p={4}
      ml={2}
      mr={2}
      color="var(--oc-gray-8)"
      borderRadius={8}
    >
      <PageMenu
        pageInfo={pageInfo}
        selectedMenu={selectedMenu}
        setSelectedMenu={setSelectedMenu}
      />
      <Divider pt={4} mb={4} variant="dashed" />
      {pageInfo[selectedMenu].component}
    </Box>
  );
}
