import { Flex, Text } from "@chakra-ui/react";
import { PageInfo } from "../utils/types";
import { useNavigate } from "react-router-dom";

interface Props {
  pageInfo: PageInfo[];
  selectedMenu: number;
  setSelectedMenu: (arg: number) => void;
  searchQueries?: { term: string; value: string }[];
}

export default function PageMenu({
  pageInfo,
  selectedMenu,
  setSelectedMenu,
  searchQueries,
}: Props) {
  const navigate = useNavigate();
  const changeURL = (i: number, id: string) => {
    setSelectedMenu(i);
    const navigateProps: Record<string, string> = {
      pathname: id,
    };
    if (searchQueries != null) {
      navigateProps.search = `?${searchQueries
        .map((q) => `${q.term}=${q.value}`)
        .join("&")}`;
    }
    navigate(navigateProps);
  };

  return (
    <Flex flexDirection={["column", "row"]} gap={[2, 6]} py={[0, 2]}>
      {pageInfo.map((info, i) => {
        const { id, label } = info;
        return (
          <Flex key={i} gap={4}>
            <Text
              variant={i === selectedMenu ? "selected" : "link"}
              onClick={() => changeURL(i, id)}
              style={{ whiteSpace: "nowrap" }}
            >
              {label}
            </Text>
            {info.isLinear && <Text fontSize="lg" mr={-2}>{`\u2192`}</Text>}
          </Flex>
        );
      })}
    </Flex>
  );
}
