import { VStack, Text, HStack } from "@chakra-ui/react";

interface Props {
  list: {
    title: string;
    desc: string;
    video: string | undefined;
  }[];
}
export default function Techniques({ list }: Props) {
  return (
    <VStack align="left">
      <Text mb={8}>
        Following techniques are used in the pattern instruction.
      </Text>
      {list.map((t, i) => (
        <HStack key={i} align="top" spacing={4}>
          <Text variant="bold">{t.title}</Text>
          <Text maxW="3xl">
            {t.desc}
            {t.video != null && (
              <Text variant="notes" ml={4}>
                <a href={t.video} target="_blank" rel="noreferrer">
                  Youtube link
                </a>
              </Text>
            )}
          </Text>
        </HStack>
      ))}
    </VStack>
  );
}
