import { ListItem, UnorderedList } from "@chakra-ui/react";

interface Props {
  overview: string[];
}
export default function PatternTextOverview({ overview }: Props) {
  return (
    <UnorderedList maxWidth="4xl">
      {overview.map((item, i) => (
        <ListItem key={i}>{item}</ListItem>
      ))}
    </UnorderedList>
  );
}
