import { HStack, VStack, Text } from "@chakra-ui/react";
import { AbbKey } from "../utils/types";
import { abbreviations } from "../stitches/abbreviation";

interface Props {
  list: AbbKey[];
}
export default function Abbr({ list }: Props) {
  return (
    <VStack align="left">
      <Text mb={8}>
        Following appreviations are used in the pattern instruction.
      </Text>
      {list
        .sort((a, b) => a.localeCompare(b, "en", { sensitivity: "base" }))
        .map((abbr, i) => (
          <HStack key={i} align="top" spacing={4}>
            <Text variant="bold">{abbr}</Text>
            <Text maxW="4xl">{abbreviations[abbr as AbbKey]}</Text>
          </HStack>
        ))}
    </VStack>
  );
}
