import { css, cx } from "@linaria/core";
import React, { MouseEvent } from "react";
import { themeBySize } from "./styles/theme";
import GlyphWrapper from "./glyphs/GlyphWrapper";
import { unit } from "./styles";
import {
  Chart,
  ChartRow,
  Direction,
  GlyphSize,
  Stitch,
  StsGroup,
  MotifColor,
} from "../utils/types";
import { motifColors } from "./styles";
import { isWSRow } from "../utils";
// import { WrittenRow } from "./WrittenPattern";

export type FocusType = [number | null, number | null];
type SetFocusType = (arg0: FocusType) => void;

interface Props {
  chart: Chart;
  direction?: Direction;
  stsCount?: number;
  focus?: FocusType;
  setFocus?: SetFocusType;
  size?: GlyphSize;
  startRowID?: number;
  className?: string;
  isOnlyRowClickable?: boolean;
}

const padding = unit / 2;

const styles = {
  wrapper: css`
    width: min-content;
  `,
  number: css`
    font-family: var(--code);
    font-weight: 700;
    font-size: var(--chart-font-size);
  `,
  chart: css`
    display: flex;
    flex-direction: column-reverse;
  `,
  rowNum: css`
    display: flex;
    align-items: center;
    cursor: default;
    width: calc(var(--chart-grid-size) * 2);
    :first-child {
      padding-left: ${padding}px;
    }
    :last-child {
      padding-right: ${padding}px;
      justify-content: flex-end;
    }
  `,
  rowNumSelected: css`
    flex-direction: column;
    background-color: var(--oc-gray-8);
    color: white;
    padding-top: ${padding / 2}px;
    padding-bottom: ${padding / 2}px;
  `,
  chartRow: css`
    display: flex;
    flex-direction: row-reverse;
  `,
  chartRowSelected: css`
    align-items: flex-end;
    border-bottom: 1px solid var(--oc-gray-8);
  `,
  stitchRepeats: css`
    display: flex;
    flex-direction: row-reverse;
  `,
  selectedSts: css`
    box-shadow: 0 0 2px 2px var(--theme-8);
    background-color: var(--theme-1);
    // Show the shadow(outline) effect over the other stitches;
    z-index: 2;
  `,
  clickable: css`
    cursor: pointer;
  `,
  stsNum: css`
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-around;
    width: 100%;
    cursor: default;
  `,
  stsGroupWrapper: css`
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  `,
  writtenRow: css`
    width: calc(100% - var(--chart-grid-size) * 6);
    padding-top: ${unit * 2}px;
    flex-wrap: wrap;
    padding-right: calc(var(--chart-grid-size) * 3);
    padding-left: calc(var(--chart-grid-size) * 3);
    border-top: 1px solid var(--oc-gray-8);
    div {
      margin-bottom: ${unit}px;
    }
  `,
  stsNumWrapper: css`
    padding-top: ${padding}px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    div {
      width: var(--chart-grid-size);
      text-align: center;
    }
  `,
};

const getMotifStyle = (id: MotifColor, rowNum: number | null): string => {
  const middleRow = css`
    /* border-right-style: solid;
    border-right-width: var(--chart-border-width);
    border-left-style: solid;
    border-left-width: var(--chart-border-width);
    margin-left: calc(-1 * var(--chart-border-width));
    margin-right: calc(-1 * var(--chart-border-width)); */
  `;
  const firstRow = css`
    border-bottom-style: solid;
    border-bottom-width: var(--chart-border-width);
    height: calc(var(--chart-grid-size) - var(--chart-border-width));
    /* svg {
      margin-bottom: calc(-1 * var(--chart-border-width));
    } */
  `;
  return `${middleRow} ${rowNum === 0 && firstRow} ${
    motifColors[id as MotifColor]
  }`;
};

function setRowStsFocus(
  event: MouseEvent,
  isOnlyRowClickable: boolean,
  selectedFocus: FocusType,
  setFocus: SetFocusType
): void {
  if (isOnlyRowClickable) {
    event.stopPropagation();
    setFocus([selectedFocus[0], null]);
  } else {
    setFocus(selectedFocus);
  }
}

function ChartRowNum({
  isSelected,
  num,
  dir,
}: {
  isSelected: boolean;
  num: string | null;
  dir: string;
}) {
  return (
    <div className={cx(styles.rowNum, isSelected && styles.rowNumSelected)}>
      {isSelected ? (
        <div>
          {num}
          <br />
          {dir}
        </div>
      ) : (
        <>{num}</>
      )}
    </div>
  );
}

export default function ChartComponent({
  chart,
  direction = "round",
  focus,
  setFocus,
  size = "L",
  startRowID = 0,
  className,
  isOnlyRowClickable = false,
}: Props) {
  let stsCount = chart[0]
    .map((stsGroup) =>
      stsGroup.sts.reduce((agg, cur) => (agg += cur.unitWidth ?? 1), 0)
    )
    .reduce((agg, cur) => (agg += cur), 0);
  return (
    <div className={cx(styles.wrapper, themeBySize[size], className)}>
      <div className={cx(styles.chart)}>
        {chart.map((row: ChartRow, i) => {
          const isRowSelected =
            focus?.[0] === i && focus?.[1] === null && setFocus != null;
          const isW = isWSRow(direction, i);
          return (
            <React.Fragment key={i}>
              <div
                className={cx(
                  styles.number,
                  styles.chartRow,
                  isRowSelected && styles.chartRowSelected
                )}
              >
                <ChartRowNum
                  isSelected={isRowSelected && !isW}
                  num={isW ? null : `${i + startRowID + 1}`}
                  dir="← RS"
                />
                {row.map((stsGroup: StsGroup, j) => (
                  <div
                    key={j}
                    className={cx(
                      styles.stitchRepeats,
                      // Interactive motif - clicked sts group
                      i === focus?.[0] &&
                        j === focus?.[1] &&
                        styles.selectedSts,
                      setFocus && styles.clickable,
                      // Patter with motifs - parts of motifs
                      stsGroup.motif != null &&
                        getMotifStyle(
                          stsGroup.motif?.id,
                          stsGroup.motif?.rowNum
                        )
                    )}
                    onClick={(e) =>
                      setFocus &&
                      setRowStsFocus(e, isOnlyRowClickable, [i, j], setFocus)
                    }
                  >
                    {stsGroup.sts.map((sts: Stitch, k) => {
                      if (sts === undefined) {
                        console.log(chart, i, row, stsGroup, j, i);
                      }
                      return <GlyphWrapper key={k} sts={sts} size={size} />;
                    })}
                  </div>
                ))}
                <ChartRowNum
                  isSelected={isRowSelected && isW}
                  num={isW ? `${i + startRowID + 1}` : null}
                  dir="WS →"
                />
              </div>
              {/* {isRowSelected && (
                <WrittenRow
                  row={row}
                  rowID={i}
                  direction={direction}
                  display="inline"
                  className={cx(styles.writtenRow)}
                />
              )} */}
            </React.Fragment>
          );
        })}
      </div>
      <div className={cx(styles.number, styles.stsNumWrapper)}>
        {Array.from({ length: stsCount }).map((_, i) => (
          <div key={i}>{(i + 1) % 100}</div>
        ))}
      </div>
    </div>
  );
}
